#about{

  .about-container {
    display: flex;
    flex-wrap: wrap;
    padding: 2rem;
    align-items: center;
  
    .left-column, .right-column {
      flex: 1;
      min-width: 300px;
      margin: 1rem;

      .profile-image {
        width: 512px;
        height: 768px;
        object-fit: cover;
        border: solid 5px #fff;
        margin: 30px 0;
        box-shadow: 0 0 20px rgb(0 0 0 / 50%);
      }

      .skills {
        margin-top: 1rem;
    
        .skill {
          display: flex;
          align-items: center;
          margin-bottom: 1rem;
          
          .skill-logo {
            width: 30px;
            height: 30px;
            margin-right: 10px;
          }
    
          h3 {
            margin: 0.5rem 0;
            flex: 1;
            color: #031a39;
          }
    
          .progress-bar {
            background-color: #f3f3f3;
            border-radius: 5px;
            overflow: hidden;
            flex: 2;
    
            .progress {
              padding: 0.5rem;
              border-radius: 5px;
              text-align: center;
              animation: fill 2s ease-out;
            }
          }
        }
      }
    }
    .right-column{
      h2{
        font-weight: 700;
        color: #031a39;
        span{
          color: #56ddfe;
        }
      }
      p{
        color: #031a39;
      }

      .study-name {
        margin-top: 30px;

        .exp-item {
          z-index: 4;
          /* position: relative; */
          background: #fff;
          border: 2px solid #56ddfe;
          padding: 40px;
          margin-bottom: 16px;
          .exp-title {
            font-size: 20px;
            text-transform: uppercase;
            margin-bottom: 14px;
          }
          .exp-off,.exp-date {
            font-size: 12px;
          }
          .exp-list ul {
            list-style-type: none !important;
            padding-left: 14px;

            li{
            
              position: relative;
              margin-bottom: 5px;
              font-size: 11px;
              padding-left: 15px;
            
              &::after {
                content: '';
                width: 5px;
                height: 1px;
                background: #000;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
              }
            }
          }
        }
      }  
    }
  }
}

  @keyframes fill {
    from { width: 0%; }
    to { width: 100%; }
  }
  
  @media (max-width: 1024px) {
    #about{
      .about-container {
        flex-direction: column;
        align-items: center;
        flex-wrap: nowrap;
      }
    }

  }
  
  @media (max-width: 768px) {
    #about{
      .about-container {
        flex-direction: column;
        align-items: center;
        flex-wrap: nowrap;
  
        .left-column, .right-column {
          width: 100%;
          margin: 0;

          .profile-image {
            max-width: 100%;
            height: auto;
            width: unset;
          }
        
          .skills {
            width: 100%;
          }
        
          .progress-bar {
            margin-bottom: 1rem;
          }
        }
      }
    }
  

  }
  